import React, {useState} from 'react';
import '../style.css';
import '../responsive.css'
import LogoImg from '../img/header-arctics-logo.png';
import { submitSubscriber } from '../../axios';
//import { successSubscribe, invalidSubmission } from './modal/subscribeModal';

export default function Header () {
    const [email, setEmail] = useState("");
    const [confirmText, setConfirmText] = useState("")
    const [popVis, setPopVis] = useState("0")

    const popText = (state) => {
        console.log()
        if (state === "3") return <p className="header__pop-text" style={{color:'red', fontWeight:'bold'}}><span className="rwd-hide" style={{margin:'0 0 0 140px'}}></span>Email</p>
        else if (state === "2") return <p className="header__pop-text" style={{color:'white', fontWeight:'bold'}}><span className="rwd-hide" style={{margin:'0 0 0 140px'}}></span>Email</p>
        else if (state === "1") return <p className="header__pop-text" style={{color:'white', fontWeight:'bold'}}><span className="rwd-hide" style={{margin:'0 0 0 140px'}}></span></p>
        else return <p className="header__pop-text" style={{color: 'transparent'}}>.</p>
    }

    const validateEmail = (input) => {
        const valid = /^[a-zA-Z0-9._:$!%-]+@[a-zA-Z0-9.-]+.[a-zA-Z]$/.test(input);
        if (valid) return true
        else return false
    }

    const handleButton = async () => {
        if (validateEmail(email)) {
            const {status, msg} = await submitSubscriber(email)
            if (status === "success") setPopVis("1")
            if (status === "error") setPopVis("2")

            setTimeout(() => { setPopVis("0") }, 1000)
        } 
        else {
            // setConfirmText("")
            setPopVis("3")

            setTimeout(() => { setPopVis("0") }, 1000)
        } 
        setEmail('');
    }

    return (
        <header id="top">
            <p className="header__slogan">
                <span className="rwd-hide"> </span>
                
                <br></br>
                <span className="header__slogan-break">
                    
                    <span className="header__highlight"></span>
                    
                <span className="rwd-hide"> </span>
                </span>
                {/*
                
                
                <span className="header__highlight">30</span>
                <br></br>
                <span className="header__slogan-break">
                    
                    <span className="header__highlight">30</span>
                    
                </span>*/}
            </p>
		    <img className="header__logo" src={LogoImg} alt="arctics"></img>
		    <h1></h1>
		    <p className="header__subtitle">
                APP
                <br className="rwd-show"></br>
                
            </p>
		    <div className="header__form">
			    <a href="#function" id="header__function-button" className="header__function-button"></a>
			    <br className="rwd-show"></br>
			    <input className="header__email-box" id="email-input" placeholder="Email" value={email} onChange={e=>setEmail(e.target.value.trim())} style={{borderColor: "red", border: "2px"}}></input>
			    <button className="header__email-button" id="email-button" onClick={handleButton}>{confirmText}</button>
		    </div>
            {popText(popVis)}
        </header>
    );
}
